import uniSwipeAction from '../../components/uni-swipe-action/uni-swipe-action.vue';
import keyboardPackage from '../../components/keyboard-package/keyboard-package.vue';
import passwordInput from '../../components/password-input/password-input.vue';
import uniNavBar from '../../components/uni-nav-bar/uni-nav-bar.vue';
import { debounce } from '@/common/utils.js';
export default {
	data() {
		return {
			options: [
				{
					text: '删除',
					style: {
						backgroundColor: '#dd524d'
					}
				}
			],
			wallet_value: '',
			wallet_key: '',
			address_out: '',
			flag: true,
			right: '',
			id: '',
			shade: true,
			password: '',
			user_id: '',
			numberList: [],
			length: 6,
			type: 'number',
			passIn: false
		};
	},
	components: {
		uniSwipeAction,
		keyboardPackage,
		passwordInput,
		uniNavBar
	},
	onLoad() {
		this.getData()
	},
	onShow() {
		this.getData()
	},
	methods: {
		getData(){
			var that = this;
			uni.request({
				url: this.url + 'walletaddresss/',
				method: 'GET',
				header: {
					Authorization: 'JWT' + ' ' + this.global_.token
				},
				success(res) {
					if (res.data.data == '') {
						that.flag = false;
					} else {
						that.flag = true;
					}
					that.address_out = res.data.data;
				}
			});
		},
		clo: function() {
			this.passIn = false;
			this.$refs['number'].close();
			this.numberList.length= 0;
		},
		onDelete() {
			this.numberList.pop();
		},
		onChange(e){
			if(e.show==false){
				this.passIn = false
			}
			this.numberList.length= 0;
		},
		onConfirm() {
			if(this.numberList.length!=6){
				uni.showToast({
					title: '请输入六位交易密码！',
					duration: 2000,
					icon: 'none'
				});
			}
		},
		onInput(val) {
			var that=this;
			that.numberList.push(val);
			that.password = that.numberList.join().replace(/,/g, '');
			if (that.numberList.length >= that.length) {
				uni.request({
					url: that.url + 'updatadeleteaddresss/',
					method: 'DELETE',
					data: {
						id: that.id,
						password: that.password
					},
					header: {
						Authorization: 'JWT' + ' ' + that.global_.token
					},
					success(res) {
						if (res.statusCode == 204) {
							that.passIn = false;
							that.$refs['number'].close();
							uni.showToast({
								title: '删除成功',
								icon: 'none',
								duration: 2000
							});
							
						}
						if(res.statusCode==400){
							that.$refs.wrong.flag=false;
							var n=res.data.data.err_num;
							var s=5-n;
							that.$refs.wrong.tip='密码错误，剩余'+ s +'次机会';
						}
						if(res.statusCode==423){
							that.passIn = false;
							that.$refs['number'].close();
							uni.showToast({
								title:'交易密码已锁定,请在今日24:00后进行交易',
								icon:'none',
								duration:2000
							})
						}
						var page = getCurrentPages().pop();
						if (page == undefined || page == null) return;
						page.onLoad();
					},
				});
				this.numberList.length = 0;
			}	 
		},
		//点击删除按钮
		click: function(item) {
			var that = this;
			that.id = item.id;
			this.passIn = true;
			this.$refs['number'].open();
			this.onInput(val);
		},
		//点击添加按钮
		linkToTransfer: debounce(function(){
			uni.navigateTo({
				url: '../add-address/add-address?flag=' + this.flag,
			});
		},500, true),
		add_address: function() {
			this.linkToTransfer()
		},
		//返回 
		back: function() {
			uni.navigateBack({
				delta: 1
			});
		},
		//点击编辑按钮
		edit: function(item) {
			var that = this;
			that.id = item.id;
			that.wallet_key = item.wallet_key;
			that.wallet_value = item.wallet_value;
			that.user_id = item.user_id;
			uni.navigateTo({
				url: '../edit-address/edit-address?id=' + that.id + '&wallet_key=' + that.wallet_key + '&wallet_value=' + that.wallet_value + '&user_id=' + that.user_id,
				
			});
		}
	}
};

